
import { defineComponent } from "vue";
import PackService from "@/services/PackService";
import { 
  PackageMetadata,
  PackageQuery,
  PackageRegEx,
} from "@/types/Pack";

export default defineComponent({
  name: "list-packages",
  data() {
    return {
      packs: [] as PackageMetadata[],
      name: "",
      version: "",
      currentPage: 1,
      totalPages: 0,
      totalPacks: 0,
      useRegex: false,
    };
  },
  methods: {
    retrievePackages(page: number) {
      const offset = page;

      if (!this.useRegex) {
        const data: PackageQuery[] = [{
          Name: this.name === "" ? "*" : this.name,
          Version: this.version,
        }];

        PackService.getPack(offset, data)
          .then((response) => {
            this.packs = response.data;
            this.totalPages = response.headers['page-count'];
            this.totalPacks = response.headers['pack-count'];
          })
          .catch((error) => {
            alert(error.response.data.message);
            window.location.reload();
        });
      } else {
        const data: PackageRegEx = {
          RegEx: this.name,
        };

        PackService.searchByRegex(data)
          .then((response) => {
            this.packs = response.data;
            this.totalPages = response.headers['page-count'];
            this.totalPacks = response.headers['pack-count'];
          })
          .catch((error) => {
            alert(error.response.data.message);
            window.location.reload();
        });
      }
    },

    searchPack() {
       this.retrievePackages(1);
       this.currentPage = 1;
     },

    paginate(page: number) {
      if (page < 1 || page > this.totalPages) {
        return;
      }

      this.currentPage = page;
      this.retrievePackages(this.currentPage);
    },
  },
  
  mounted() {
    this.totalPages = 0;
    this.totalPacks = 0;
    this.useRegex = false;
    this.retrievePackages(this.currentPage);
  },
});
